"use strict"

import TypewriterSum from "./typewriter-sum.js";

export default class TypewriterBigint extends TypewriterSum{

    static checkUserBigInt(user_value) {
        document.getElementById("resultBigint").innerText = "10";
        try {
            BigInt(user_value);
            return true
        } catch (err) {
            if (err.name === "SyntaxError") {
                return false
            } else throw err;
        }
    }

    constructor(first) {
        super ("RUB",first);
        document.getElementById("resultBigint").innerText = "5";
        this.first = BigInt(first).toString();
        document.getElementById("resultBigint").innerText = "6";
    }
}