"use strict"

import {converterBigInt} from "./converter-bigint.js";
import {catchUncaughtDOMException} from "./working-with-results.js";

// Функция анимации вывода результата для счета.
function printResult3() {
    setTimeout(function () {
        document.getElementById("resultBigint").innerText = resultBigNum.slice(0, y);
        if (document.getElementById("resultBigint").innerText !== resultBigNum) {
            y += 1;
            window.requestAnimationFrame(printResult3)
        }
    }, 3)
}

// Блок кода с реализацией вывода результата большого числа.
const form2 = document.getElementById("second-form");
let resultBigNum;
let y;

form2.addEventListener("submit", event => {
    event.preventDefault();
    resultBigNum = converterBigInt(form2.elements.user_value.value);
    if (!resultBigNum) {
        let y = "Введите допустимое значение."
        document.getElementById("resultBigint").innerText = y;
    } else {
        y = 0;
        try {
            window.sessionStorage.setItem("storage2",
                window.JSON.stringify([form2.elements.user_value.value, resultBigNum]));
        } catch (err) {
            catchUncaughtDOMException(err);
        }
        window.requestAnimationFrame(printResult3);
    }
});

// Реализация кнопки "Очистить" второй формы.
form2.addEventListener("reset", event => {
    event.preventDefault();
    document.getElementById("second-form").elements.user_value.value = "";
    resultBigNum = "";
    document.getElementById("resultBigint").innerText = resultBigNum;
    try {
        window.sessionStorage.setItem("storage2",
            window.JSON.stringify([document.getElementById("second-form").elements.user_value.value, resultBigNum]));
    } catch (err) {
        catchUncaughtDOMException(err);
    }
});