"use strict"

import TypewriterBigint from "./typewriter-bigint.js";

export function converterBigInt(user_value) {
    // Фрагмент кода удаляет пробелы.
    user_value = user_value.replace(/\s/g, x => "");
    document.getElementById("resultBigint").innerText = "2";
    // Проверка на допустимость вводимого значения
    if (!TypewriterBigint.checkUserBigInt(user_value)) {
        return false;
    }
    document.getElementById("resultBigint").innerText = "-1";
    // Основной код функции.
    return  `${new TypewriterBigint(user_value)}`;
}